<template>
    <div>
        <a-card :bordered="false">
            <h3></h3>
            <a-row :gutter="20">
                <a-col :span="4">
                    <a-cascader allowClear :options="cardNumber_options" placeholder="" @change="getArticleList"
                        v-model="querydata.card_number" />
                </a-col>
                <a-col :span="4">
                    <!-- <a-input-search placeholder="" enter-button v-model="querydata.transaction_type" la
                        @search="getArticleList"></a-input-search> -->
                    <a-cascader allowClear :options="options" placeholder="" @change="getArticleList"
                        v-model="querydata.transaction_type" />
                </a-col>
                <a-col :span="5">
                    <a-cascader allowClear :options="bridgeOptions" placeholder="FB" @change="getArticleList"
                        v-model="querydata.is_judge" />
                </a-col>
                <a-col :span="5">
                    <a-range-picker @change="getArticleList"  v-model="time_range"/>
                </a-col>
                <a-col :span="2">
                    <a-button type="primary" @click="getArticleList"></a-button>
                </a-col>
                <a-col :span="2">
                    <a-button type="primary" @click="getClear"></a-button>
                </a-col>
            </a-row>
            <!--  -->
            <a-button type="primary" @click="showDialog" style="margin-top:10px ;"></a-button>
            <a-modal v-model:visible="dialogVisible" title="" @ok="handleOk">
                <p></p>
                <a-upload-dragger v-model:fileList="fileList" name="f1" :multiple="true"
                    action="http://111.90.143.10:39010/api/v1/upload1" @change="handleChangeFile">
                    <p class="ant-upload-drag-icon">
                        <a-icon type="cloud-download" />
                    </p>
                    <p class="ant-upload-text"></p>
                    <p class="ant-upload-hint">
                        *xls
                    </p>
                </a-upload-dragger>
                <div class="upload-tips">
                    <p>1.*xls</p>
                    <p>2.5000</p>
                    <p>3.3M</p>
                </div>
            </a-modal>
            <a-table :rowKey="(record, index) => { return index }" :columns="columns" :pagination='paginationOption'
                :dataSource="Articlelist" bordered>
                <template slot="is_judge" slot-scope="text">
                    <!--  -->
                    <span :style="{ color: !text.is_judge ? 'green' : 'red' }">
                        {{ !text.is_judge ? '' : '' }}
                    </span>
                </template>
            </a-table>
        </a-card>

    </div>
</template>

<script>
import { message } from 'ant-design-vue';
const moment = require('moment'); 
const columns = [
    {
        title: 'ID',
        dataIndex: 'transaction_id',
        width: '15%',
        key: 'transaction_id',
        align: 'center',
    },
    {
        title: '',
        dataIndex: 'transaction_time',
        width: '15%',
        key: 'transaction_time',
        align: 'center',
    },
    {
        title: '',
        dataIndex: 'card_number',
        width: '10%',
        key: 'card_number',
        align: 'center',

    },

    {
        title: '',
        dataIndex: 'transaction_type',
        width: '10%',
        key: 'transaction_type',
        align: 'center',
    },
    {
        title: 'order-amount',
        dataIndex: 'order_amount',
        width: '10%',
        key: 'order_amount',
        //scopedSlots: { customRender: 'img' },
        align: 'center',
    },
    {
        title: 'order-currency',
        dataIndex: 'order_currency',
        width: '10%',
        key: 'order_currency',
        //scopedSlots: { customRender: 'img' },
        align: 'center',
    },
    {
        title: 'result_description',
        dataIndex: 'result_description',
        width: '10%',
        key: 'result_description',
        //scopedSlots: { customRender: 'img' },
        align: 'center',
    },
    {
        title: 'fb',
        dataIndex: 'is_judge',
        width: '10%',
        key: 'is_judge',
        scopedSlots: { customRender: 'is_judge' },
        //  scopedSlots  
        align: 'center',
    },

]
export default {
    data() {
        return {
            labelCol: { span: 5 },
            wrapperCol: { span: 16 },
            paginationOption: {
                pageSizeOptions: ['5', '10', '20'],
                defaultCurrent: 1,
                defaultPageSize: 5,
                showSizeChanger: true,
                showTotal: (total) => `${total}`,
                onChange: (current, size) => {
                    console.log(current, size, '=====pppp')
                    this.paginationOption.defaultCurrent = current
                    this.paginationOption.defaultPageSize = size
                    this.getArticleList()

                },
                onShowSizeChange: (current, size) => {
                    this.paginationOption.defaultCurrent = 1
                    this.paginationOption.defaultPageSize = size
                    this.getArticleList()
                },
                total: 0
            },
            Articlelist: [],
            Catelist: [],
            columns,
            querydata: {
                pagesize: 0,
                pagenum: 0,
                card_number: '',
                transaction_type: '',
                start_time:'',
                end_time:'',
                is_judge:''
                // title: '',
            },
            //time_range:[],
            time_range:[],
            dialogVisible: false, //   
            fileList: [], //   
            vccBalance: 0,
            vccVisible: false,
            options: [{
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }, {
                value: '',
                label: ''
            }],
            bridgeOptions:[{
                value:1,
                label:''
            },{
                value:0,
                label:''
            }],
            cardNumber_options: []
        }
    },
    created() {
        this.getArticleList()
        this.getCardNumberOptions()
    },
    methods: {
        //   
        showDialog() {
            this.dialogVisible = true;
        },
        handleChangeFile(info) {
            const status = info.file.status;
            if (status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (status === 'done') {
                message.success(`${info.file.name} `);
            } else if (status === 'error') {
                message.error(`${info.file.name} `);
            }
        },
        async showVccBalance(id) {
            const result = await this.$http.post('showVccBalance', { card_number: id });
            console.log(result, result.data.dsg.Number, '=====111')
            if (result.status != 200) return this.$message.error(result.message)
            this.vccBalance = result.data.dsg.Number
            console.log(this.vccBalance)
            this.vccVisible = true
        },
        handleOk(e) {
            console.log(e);
            this.dialogVisible = false;
        },
        //
        async getArticleList() {
            console.log(this.time_range,'=====>time')
            this.querydata.pagesize = this.paginationOption.defaultPageSize
            this.querydata.pagenum = this.paginationOption.defaultCurrent
            this.querydata.start_time = (this.time_range[0])?.startOf('day').unix()
            this.querydata.end_time =(this.time_range[1])?.endOf('day').unix()
            const is_judge = this.querydata.is_judge === '' ? -1 : this.querydata.is_judge
            const url = `showvcc_record?pagesize=${this.querydata.pagesize}&pagenum=${this.querydata.pagenum}&card_number=${(this.querydata.card_number || [])}&transaction_type=${(this.querydata.transaction_type || [])}&start_time=${this.querydata.start_time}&end_time=${this.querydata.end_time}&is_judge=${is_judge}`;
            const { data: result } = await this.$http.get(url);
            if (result.code != 200) return this.$message.error(result.message)
            this.Articlelist = result.data
            this.paginationOption.total = result.total
        },
        async getCardNumberOptions() {
            const { data: result } = await this.$http.get('showVccID')
            if (result.code != 200) return this.$message.error(result.message)
            this.cardNumber_options = result.data.map(number => ({
                value: number,
                label: number
            }))
            console.log(this.cardNumber_options,'======>123')
        },
        getClear() {
            this.querydata = {
                pagesize: 0,
                pagenum: 0,
                card_number: '',
                transaction_type: '',
                is_judge:-1
            }
            this.time_range=[]
            this.getArticleList()
        },
        async findArticle() {
            this.querydata.pagesize = this.paginationOption.defaultPageSize
            this.querydata.pagenum = 1
            const { data: result } = await this.$http.post('articles', this.querydata)
            if (result.status != 200) return this.$message.error(result.message)
            this.Articlelist = result.data
            this.paginationOption.total = result.total
        },
        //
        deleteArticle(id) {
            this.$confirm({
                title: "",
                content: "",
                onOk: async () => {
                    const { data: result } = await this.$http.delete(`article/${id}`)
                    if (result.status != 200) return this.$message.error(result.message)
                    this.$message.success('')
                    this.getArticleList()
                },
                onCancel: () => {
                    this.$message.info("")
                }
            })
        },
        //
        async getCateList() {
            const { data: result } = await this.$http.get('category')
            if (result.status != 200) return this.$message.error(result.message)
            this.Catelist = result.data
            this.pagination.total = result.total
        },
        //
        CateChange(value) {
            this.getCateArticle(value)
        },
        async getCateArticle(id) {
            const { data: result } = await this.$http.get(`article/cate/${id}`, {
                params: {
                    pagesize: this.paginationOption.defaultPageSize,
                    pagenum: this.paginationOption.defaultCurrent
                }
            })
            if (result.status != 200) return this.$message.error(result.message)
            this.Articlelist = result.data
            this.paginationOption.total = result.total
        },
        //   
        showDialog() {
            this.dialogVisible = true;
        }
    },
}
</script>

<style scoped>
.actionSlot {
    display: flex;
    justify-content: center;
}

::v-deep .ant-cascader-menu {
    height: 200px;
}
</style>
